@font-face {
  font-family: "Montserrat";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 100%;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}
@media (prefers-reduced-motion: no-preference) {
  html {
    scroll-behavior: smooth;
  }
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: #011c40;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f9fbfd;
  -webkit-text-fill-color: #011c40;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

:root {
  --side-offset: calc(50vw - 580px);
}
@media (max-width: 1249px) {
  :root {
    --side-offset: 45px;
  }
}
@media (max-width: 767px) {
  :root {
    --side-offset: 15px;
  }
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.btn {
  display: block;
  padding: 10px 20px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 150%;
  font-weight: 600;
  text-align: center;
  color: #011c40;
  user-select: none;
  background-color: #f9fbfd;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}
.btn:active {
  background-color: #bdbdbe;
}

.main-header {
  position: absolute;
  left: 0;
  right: 0;
  z-index: 2;
  display: flex;
  justify-content: space-between;
  padding: 35px calc(var(--side-offset) - 13px) 35px var(--side-offset);
}
@media (max-width: 1023px) {
  .main-header {
    padding: 30px calc(var(--side-offset) - 10px) 30px var(--side-offset);
  }
}
@media (max-width: 767px) {
  .main-header {
    display: block;
    padding: 20px calc(var(--side-offset) - 5px) 20px var(--side-offset);
  }
}
@media (max-width: 767px) {
  .main-header--open {
    top: 0;
    right: 0;
    left: auto;
    z-index: 3;
    min-width: 320px;
    margin-left: 35px;
    background-color: #f9fbfd;
    transition: 0.4s all;
  }
}

.main-header__menu {
  display: flex;
  justify-content: space-between;
}

.main-header__button {
  display: none;
  padding: 5px;
  border: none;
  background-repeat: no-repeat;
  background-color: transparent;
  background-position: center;
}
.main-header__button:active {
  opacity: 0.6;
}
@media (max-width: 767px) {
  .main-header__button--open {
    width: 34px;
    height: 24px;
    background-image: url("../img/svg/icon-open.svg");
  }
}
@media (max-width: 767px) {
  .main-header__button--close {
    display: block;
    width: 25px;
    height: 25px;
    background-image: url("../img/svg/icon-close.svg");
  }
}

