  font-weight: 400;
  color: #011c40;
  max-width: 355px;
  font-size: 18px;
}
@media (max-width: 1023px) {
  .feedback__text-holder p {
    margin-bottom: 60px;
  }
}
@media (max-width: 767px) {
  .feedback__text-holder p {
    margin-bottom: 40px;
  }
}
@media (max-width: 1023px) {
  .feedback__text-holder p {
