.form {
  max-width: 403px;
  padding: 43px 50px 53px;
  background-color: #011c40;
}
@media (max-width: 1023px) {
  .form {
    max-width: none;
    margin: 0 8px;
  }
}
@media (max-width: 767px) {
  .form {
    margin: 0;
    padding: 34px 30px 40px;
  }
}
.form h2 {
  margin: 0 0 27px;
  font-size: 18px;
  line-height: 150%;
  font-weight: 500;
  color: #f9fbfd;
}
@media (max-width: 767px) {
  .form h2 {
    line-height: 140%;
    text-align: center;
  }
}
.form input {
  display: block;
  width: 100%;
  min-height: 43px;
  margin-bottom: 20px;
  padding: 11px 16px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  background-color: #f9fbfd;
  border: none;
}
.form input:hover {
  opacity: 0.8;
}
.form input:active {
  background-color: #bdbdbe;
}
.form input:disabled {
  opacity: 0.5;
}
.form input::placeholder {
  color: #011c40;
}

.form__fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

.form__checkbox {
  position: relative;
  min-height: 18px;
  margin-bottom: 30px;
}
@media (max-width: 1023px) {
  .form__checkbox {
    padding: 4px 0 3px;
  }
}
@media (max-width: 767px) {
  .form__checkbox {
    margin-bottom: 28px;
    padding: 0;
  }
}
.form__checkbox label {
  display: flex;
  align-items: center;
}
.form__checkbox label:hover {
  opacity: 0.8;
}
.form__checkbox label:active {
  opacity: 0.6;
}

.form__mark-checkbox {
  position: absolute;
  content: "";
  width: 18px;
  height: 18px;
  background-color: #f9fbfd;
}
@media (max-width: 767px) {
  .form__mark-checkbox {
    width: 20px;
    height: 20px;
  }
}

.form__checkbox input:checked + .form__mark-checkbox {
  background-image: url("../img/svg/checkbox.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.form__text {
  display: block;
  padding-left: 23px;
  font-size: 10px;
  line-height: 130%;
  color: #f9fbfd;
}
@media (max-width: 767px) {
  .form__text {
    padding-left: 28px;
    line-height: 120%;
  }
}

.form__button {
  width: 100%;
}

.contacts {
  display: grid;
  grid-template-columns: 1.12fr 1fr;
  align-items: center;
  gap: 8.6%;
  padding: 120px var(--side-offset);
}
@media (max-width: 1023px) {
  .contacts {
    grid-template-columns: 1fr 1fr;
    gap: 30px;
    padding: 100px var(--side-offset);
  }
}
@media (max-width: 767px) {
  .contacts {
    display: flex;
    flex-direction: column-reverse;
    padding: 59px var(--side-offset) 65px;
  }
}

.contacts__map {
  width: 100%;
  height: 306px;
}
@media (max-width: 1023px) {
  .contacts__map {
    height: 335px;
  }
}
@media (max-width: 767px) {
  .contacts__map {
    height: 316px;
  }
}

.contacts__text h3 {
  margin: 0 0 32px;
  font-size: 18px;
  line-height: 150%;
  font-weight: 500;
}
@media (max-width: 1023px) {
  .contacts__text h3 {
    margin: 0 0 26px;
  }
}
.contacts__text p {
  margin: 0;
  margin-bottom: 20px;
  font-size: 16px;
  line-height: 150%;
  font-weight: 400;
  color: #011c40;
  position: relative;
  padding-left: 28px;
}
@media (max-width: 1023px) {
  .contacts__text p {
    margin-bottom: 20px;
  }
}
@media (max-width: 767px) {
  .contacts__text p {
    margin-bottom: 22px;
  }
}
.contacts__text p::before {
  position: absolute;
  left: 0;
  width: 18px;
  height: 22px;
  content: "";
