}
@media (max-width: 1023px) {
  .contacts__text p::before {
    top: 5px;
  }
}
.contacts__text a {
  margin: 0;
  margin-bottom: 3px;
  font-size: 16px;
  line-height: 150%;
  font-weight: 400;
  color: #011c40;
  display: inline-block;
  position: relative;
  padding-left: 28px;
}
@media (max-width: 1023px) {
  .contacts__text a {
    margin-bottom: 2px;
  }
}
@media (max-width: 767px) {
  .contacts__text a {
    margin-bottom: 6px;
  }
}
.contacts__text a:hover {
  opacity: 0.8;
}
.contacts__text a:active {
  opacity: 0.6;
}
.contacts__text a::before {
  position: absolute;
  left: 0;
  width: 18px;
  height: 18px;
  content: "";
  background-image: url("../img/svg/contact-phone.svg");
}

.main-footer {
  display: grid;
  grid-template-columns: 70px auto max-content;
  gap: 27px;
